import API from "../axios/API";
import Auth from "../modules/Auth";
import { MD5 } from "crypto-js";


export const login = async (username, password) => {
  const body = {
    credential: {
      username: username,
      password: MD5(password).toString()
    }
  };
  // console.log('url: '+ process.env.REACT_APP_SERVER_URL + "/api/quan-tri/da-users/login");
  
  return await API({
    method: "GET",
    url: process.env.REACT_APP_SERVER_URL + "/api/quan-tri/da-users/login",
    data: body
  }).then(res => {
    console.log('res: ' + res);
    Auth.setUserToken(res.data.user_token);
    return res;
  });
};

export const register = async (fullname, email, password, verifyPassword) => {
  return await API({
    method: "POST",
    url: "/users/signin",
    data: {
      fullname,
      email,
      password,
      verifyPassword
    }
  }).then(res => {
    // Auth.setUserToken(res.data.user_token);
    console.log(res);
    return res;
  });
};
